@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Adamina&display=swap');

@media screen and (min-width: 776px) {
  ::-webkit-scrollbar {
    width: 6px;
    background: #F7F5F4;
  }

  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 0.5rem #F7F5F4;
    border-radius: 1rem;
  }

  ::-webkit-scrollbar-thumb {
    background: #080A0B;
    border-radius: 1rem;
  }

  /*   
    ::-webkit-scrollbar-thumb:hover {
      background: #080A0B;
      border-radius: 1rem;
    } */
}

.slides {
  transition: ease 1000ms;
}

.typewriter {
  overflow: hidden;
  border-right: .07em solid #080A0B;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .1em;
  animation:
    typing 3s steps(40, end),
    blink-caret .75s step-end infinite;
}

@keyframes typing {
  from {
    width: 0
  }

  to {
    width: 100%
  }
}

@keyframes blink-caret {

  from,
  to {
    border-color: transparent
  }

  50% {
    border-color: #080A0B;
  }
}

.navbar-phone-icon {
  transition: transform 0.3s ease;
}

.navbar-phone-icon.open {
  transform: rotate(90deg);
}

.navbar-menu {
  transition: transform 0.8s ease;
}

.navbar-menu.open {
  transform: translateY(0);
}

.navbar-menu.closed {
  transform: translateY(-100%);
}

.animate-flick {
  animation: flick 3s infinite
}

@keyframes flick {
  from {
    opacity: 70%;
  }

  50% {
    opacity: 100%;
  }

  to {
    opacity: 70%;
  }
}

.card-tools {
  transition: transform 2s;
}

.card-tools:hover {
  transform: rotateY(360deg);
}

.slider-container {
  display: flex;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  background-color: #080A0B;
}

@layer utilities {

  /* Chrome, Safari Opera */
  .no-scrollbar::-webkit-scrollbar {
    display: none;
  }

  .no-scrollbar {
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
  }
}